* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  width: 100%;
  height: auto;
}

input {
  font: inherit;
  padding: 8px;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}

li {
  margin-bottom: 4px;
  list-style-type: none;
  display: flex;
}

ul {
  margin-left: 0;
  padding-left: 0;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(200px, auto);
  gap: 14px;
  display: grid;
  overflow: auto;
}

.photo-card {
  border-radius: 0;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.gallery__image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  max-width: 100%;
  display: block;
}

.info {
  text-align: center;
  background-color: rgba(247, 247, 247, .9);
  justify-content: space-between;
  align-items: center;
  padding: 10px 6px;
  display: flex;
}

.info-item {
  color: #333;
  margin: 0;
  font-size: 12px;
  line-height: 1.5;
}

.cards-link {
  flex-direction: column;
  text-decoration: none;
  display: flex;
}

.photo-card:hover .gallery__image {
  transition: transform .3s ease-in-out;
  transform: scale(1.1);
}

